                        [teamB]: R.reduce((a, b) => +a + +(b.value === '2PTM' ? 2 : 3), 0, selectedGame.items.filter(item => (item.item_code === 'PBP' || item.code === 'PBP') && JSON.parse(item.quantity).team === 'away' && (item.value === '2PTM' || item.value === '3PTM') && (JSON.parse(item.quantity)?.sech === true))),
